.header-library {
background: #0000008F;
padding-top: 40px;
padding-bottom: 62px;

background-repeat: no-repeat;

background-size: cover;
background-position: center;

background-image: linear-gradient(rgba(0, 0, 0, 0.56), rgba(0, 0, 0, 0.56)),
url(../images/Mobile/watched-mob-background.jpg);

@media screen and (min-device-pixel-ratio: 2),
(min-resolution: 192dpi),
(min-resolution: 2dppx) {
    background-image: linear-gradient(rgba(0, 0, 0, 0.56), rgba(0, 0, 0, 0.56)),
    url(../images/Mobile/watched-mob-background-x2.jpg);
}

@media screen and (min-device-pixel-ratio: 3),
(min-resolution: 288dpi),
(min-resolution: 3dppx) {
    background-image: linear-gradient(rgba(0, 0, 0, 0.56), rgba(0, 0, 0, 0.56)),
    url(../images/Mobile/watched-mob-background-x3.jpg);
}

@include for-size(tablet) {
    background-image: linear-gradient(rgba(0, 0, 0, 0.56), rgba(0, 0, 0, 0.56)),
    url(../images/Tablet/watched-tablet-background.jpg);

@media screen and (min-device-pixel-ratio: 2),
(min-resolution: 192dpi),
(min-resolution: 2dppx) {
    background-image: linear-gradient(rgba(0, 0, 0, 0.56), rgba(0, 0, 0, 0.56)),
    url(../images/Tablet/watched-tablet-background-x2.jpg);
}

@media screen and (min-device-pixel-ratio: 3),
(min-resolution: 288dpi),
(min-resolution: 3dppx) {
    background-image: linear-gradient(rgba(0, 0, 0, 0.56), rgba(0, 0, 0, 0.56)),
    url(../images/Tablet/watched-tablet-background-x3.jpg);
}
}

@include for-size(desktop) {
    background-image: linear-gradient(rgba(0, 0, 0, 0.56), rgba(0, 0, 0, 0.56)),
    url(../images/Desctop/watched-background.jpg);

    @media screen and (min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
        background-image: linear-gradient(rgba(0, 0, 0, 0.56), rgba(0, 0, 0, 0.56)),
        url(../images/Desctop/watched-background-x2.jpg);
    }

    @media screen and (min-device-pixel-ratio: 3),
    (min-resolution: 288dpi),
    (min-resolution: 3dppx) {
        background-image: linear-gradient(rgba(0, 0, 0, 0.56), rgba(0, 0, 0, 0.56)),
        url(../images/Desctop/watched-background-x3.jpg);
    }

    
}


}

.header-library__container {
}

.library-navigation {
display: flex;
justify-content: space-between;
align-items: center;

margin-bottom: 60px;
}

.library-navigation__svg {

}

.library-navigation__logo {
display: flex;
align-items: center;
}


.library-logo__svg {

}

.library-navigation__text {
    font-weight: 500;
    font-size: 30px;
    line-height: 1.16;
    color: #fff;
    margin-right: 10px;
    
    @include for-size(tablet-up) {
        display: none;
    }
    
    @include for-size(tablet) {
        display: block;
    }

}

.library-navigation__list {
display: flex;



}

.library-navigation__item {
    font-weight: 500;
    font-size: 12px;
    line-height: 1.16;
        position: relative;
&:not(:last-child) {
    margin-right: 40px;
}
}

.library-navigation__link {
    color: #FFFFFF;
    text-transform: uppercase;
}

.header-library__button-container {
}

.header-library__button-list {
display: flex;
justify-content: space-between;
align-items: center;

@include for-size(tablet) {
justify-content: center;
}

}

.header-library__button-item {
@include for-size(tablet) {
&:not(:last-child) {
    margin-right: 32px;
}}


}

.header-library__button {
display: flex;
justify-content: center;
align-items: center;
background: transparent;
border: 1px solid #FFFFFF;
color: #FFFFFF;
padding-top: 14px;
padding-bottom: 14px;
width: 129px;

@include for-size(tablet) {
padding-top: 13px;
padding-bottom: 13px;
width: 152px;
}

@include for-size(desktop) {
width: 148px;
}

&:hover, &:focus {
    background-color: #FF6B01;
    box-shadow: 0px 8px 43px rgba(255, 107, 1, 0.6);
    border: 1px solid transparent;
}
}

.current::after {
    position: absolute;
    display: block;
    content: '';
    width: 100%;
    left: 0;
    bottom: -5px;
    height: 4px;
    background: #FF6B08;
    border-radius: 2px;
}